/**
 * api
 */
import axios from '@/utils/http'; // httpaxios

const ajaxList = {
    // 
    currencyForm(query) {
        return axios('post', 'currency/form', query)
    },
    // 
    login(query) {
        return axios('post', 'user/login', query)
    },
    // 
    register (query){
        return axios('post', 'user/register', query)
    },
    // 
    registerCheck (query){
        return axios('post', 'user/check', query)
    },
    // 
    demoRegister (query){
        return axios('post', 'user/demoRegister', query)
    },
    // 
    Collection (query){
        return axios('post', '/user/collect', query)
    },
    // 
    loginOut (query){
        return axios('get', 'user/loginOut', query)
    },
    // 
    userDetails (query){
        return axios('get', 'user/userDetails', query)
    },
    // 
    trade (query){
        return axios('post', 'trade/trade', query)
    },
    // 
    getOrderData (query){
        return axios('get', 'trade/select', query)
    },
    // 
    getPreviousData (query){
        return axios('post', 'period/road', query)
    },
    // 
    getTradeVolume (query, url){
        return axios('get', `trade/tradeVolume/${url.market}/${url.code}/${url.type}`)
    },
    // 
    getTradeRank (query){
        return axios('get', 'trade/ranking', query)
    },
    //
    getOdds (query) {
        return axios('post', 'product/getRatio', query)
    },
    //
    ProductList (query) {
        return axios('get', 'product/getRatioAll', query)
    },
    // 
    getBeforeEnterData (query){
        return axios('post', '/period/beforePrice', query)
    }
}

export default ajaxList;